.container{
  width:100%;
  height: 100%;
  position: relative;
  .topContainer{
    background-color:#E6EAF0;
    .top{
      height:120px;
      width:1180px;
      margin:0 auto;
      position: relative;
      :global(.ant-breadcrumb){
        text-align: left;
        width:200px;
        position: absolute;
        top:34px;
        font-size: 16px;
        :global(.anticon-shopping){
          margin-right: 8px;
        }
      }
      .right{
        position: absolute;
        right: 0;
        text-align: right;
        width:50%;
        line-height:120px;
        .add{
          width:102px;
          height:40px;
          margin-right:3.9%;
          background-color:#6597FF;
          font-size: 14px;
          font-family: Roboto;
          vertical-align: middle;
        }
        .search{
          width:251px;
          height:36px;
          vertical-align: middle;
          color:#888888;
          :global(.ant-input-suffix){
            font-size:20px;
          }
        }
      }
    }
  }
    .table {
      background-color: #FFFFFF;
      width: 1180PX;
      margin: 24px auto;
      :global(.ant-table-thead){
        background-color:#F0F2F5;
      }
      :global(.ant-table-thead tr th){
        background-color:#F0F2F5;
      }
      :global(.userName .ant-table-column-sorter){
          left:70px;
      }
      :global(.ant-table-column-sorter){
        color: #101010  !important;
        left:54px;
      }
      :global(.ant-table-tbody tr:nth-child(even)){
        background-color: rgba(240, 242, 245, 0.5);
      }
      :global(.ant-table-thead  tr   th){
        padding: 10px 8px !important;
        min-width: 113px;
      }
      :global(.ant-table-thead  tr   th:last-child){
        min-width: 180px;
      }
      td, div {
        color: rgba(16, 16, 16, 1);
        font-family: SourceHanSansSC-regular;
        font-size: 14px;
      }
      td{
        padding: 10px 8px !important;
      }
      a {
        color: rgba(101, 151, 255, 1);
        text-decoration: none;
      }
      :global(.ant-table-placeholder){
        text-align: center!important;
      }
    }
  :global(.ant-pagination-total-text) {
    margin-top: -18px;
    flex: 1;
    margin-right:0!important;
    color: rgba(16, 16, 16, 1);
    font-size: 14px;
    text-align: left;
  }
  :global(.ant-pagination) {
    width:1180px;
    margin: 27px auto;
    display:flex;
  }

}

:global(.ant-modal-mask) {
  .ant-modal-wrap {
    :global(.ant-modal-content){
      margin: 0 auto;
      height: 280px !important;
      width: 420px !important;
      .ant-modal-header {
        height:40px;
        background: #709DFB;
        color: #fff;
      }
      .ant-modal-close {
        .ant-modal-close-x {
          height: 40px;
          color: #fff;
          line-height: 40px;
        }
      }
      .ant-modal-body {
        padding: 57px 0 0 0;
        height: 193px;

        p {
          text-align: center;
        }
      }
      .ant-modal-footer {
        border-top: 0 solid #e8e8e8;
        padding: 0 16px;
        .ant-btn {
          height: 36px;
          width: 120px;
          color: #709DFB;
          border-color: #709DFB;
        }
      }
    }
  }
}